.App {
  text-align: center;
}



#nav #navM{position:sticky;top:0}
.nav{
  display:inline;
  margin-left: 10px;
  color:black;
  
}
.nav:hover{background:grey;color:white;}
#links{

  margin-left:5vw;
  margin-right:5vw;
  list-style-type: none;
 
  padding: 0;
  width: 90vw;
  background-color: #f1f1f1;

}
li{
  display: block;
  color: #000;
  padding: 8px 16px;
 
  color:black;
  border:1px solid black;
 

}
li:hover{
  background: grey;
  color:white;

}
Link{
  text-decoration: none;
}
#margin{
  display:grid;
  grid-template-columns:33% auto;
}
